import React from "react";

const Contact =() =>{
    return(
        <>
       <div className="contact_sec1">
           <div className="row m-0">
                    <div className="col-12 col-md-4">
                         <h3>Contact Us</h3>
                         <p>Acme Outdoors <br/> 123 Rainy Street <br/>Oklahoma City, OK 73129</p>
                         <h6> <b> General Inquiries: </b> (405) 555-5555</h6>
                         <h6> <b>Customer Support: </b>  (405) 555-5556</h6>
                    </div>
                    <div className="col-12 col-md-8" id="form">
                        <h3>Contact Form</h3>
                        <p>Send us a message and we'll get back to you as soon as we can!</p>
                        <div className="mb-3">
                        <label for="formGroupExampleInput" className="form-label">Name</label>
                        <input type="text" className="form-control" id="formGroupExampleInput" placeholder="Enter Your Name"/>
                        </div>
                        <div className="mb-3">
                        <label for="formGroupExampleInput2" className="form-label">Email Address</label>
                        <input type="text" className="form-control" id="formGroupExampleInput2" placeholder="Enter Your Email"/>
                        </div>
                        <div className="mb-3">
                        <label for="validationTextarea" className="form-label">Your Message</label>
                        <textarea className="form-control" id="validationTextarea" placeholder="Enter your message" required></textarea>
                        </div>
                        <button className="btn" type="button">Submit</button>
                    </div>          
           </div>
       </div>
        </>

    );
};

export default Contact;