import React from "react";
import Tent from "./Tent";
import Gift from "./Gift";
import Accesories from "./Accesories";
import Packages from "./Packages";
import { Switch, Route } from "react-router-dom";
import { NavLink } from "react-router-dom";
const Shop =() =>{
    return(
        <>
        <div className="shop_sec1">
            <h1>Shop our products</h1>
            <div className="row m-0">
                <div className="col-12 col-md-10 m-auto">
                    <div className="row">
                        <div className="col-12 addbg">
                            <p>white tent <br/> $200 USD</p>
                            <button className="btn btn-light">Featured Items</button>
                        </div>
        
                        <div className="col-12 col-md-3 shop_btn">
                            <h5>Shop by Category</h5>
                            <NavLink exact activeClassName="btnactive" to="/shop"> <button className="btn btn-outline-dark w-100">Gift Cards</button></NavLink>
                           <NavLink exact activeClassName="btnactive" to="/shop/gift"><button className="btn btn-outline-dark w-100">Tents</button></NavLink> 
                          <NavLink exact activeClassName="btnactive" to="/shop/accesories"><button className="btn btn-outline-dark w-100">Accesories</button></NavLink>  
                         <NavLink exact activeClassName="btnactive" to="/shop/packages"> <button className="btn btn-outline-dark w-100">Packages</button></NavLink>  
                            </div>     
                            <div className="col-12 col-md-9">
                            {/* <div className="row m-0">
                            <div className="col-12 col-md-4 shop_product3">
                            <div className="box2"></div>
                            <p>white tent</p>
                            <h6>$ 200 USD</h6>
                            <button className="btn" type="button">Details</button>
                            </div>
                            <div className="col-12 col-md-4 shop_product3">
                            <div className="box2"></div>
                            <p>white tent</p>
                            <h6>$ 200 USD</h6>
                            <button className="btn" type="button">Details</button>
                            </div>
                            <div className="col-12 col-md-4 shop_product3">
                            <div className="box2"></div>
                            <p>white tent</p>
                            <h6>$ 200 USD</h6>
                            <button className="btn" type="button">Details</button>
                            </div>

                            </div> */}
                            <Switch>
                            <Route exact path="/shop" component={ Tent }/> 
                            <Route exact  path="/shop/gift" component={ Gift }/> 
                            <Route exact path="/shop/accesories" component={ Accesories }/> 
                            <Route exact path="/shop/packages" component={ Packages }/> 
                            </Switch>
                        </div> 
                    </div>
                </div>
            </div>
        </div>
        </>

    );
};

export default Shop;