import React, { useState } from "react";
import { NavLink } from "react-router-dom";
import { FontAwesomeIcon } from '../node_modules/@fortawesome/react-fontawesome';
import {faShoppingCart } from '../node_modules/@fortawesome/free-solid-svg-icons';
import footer_img from "./img/logo2.svg"
const Navbar =() =>{
    
    const [display, setDisplay] = useState(false);
    
    return(
        <>
        
        <nav className="navbar navbar-expand-lg navbar-light bg-light">
        <div className="container-fluid">
            <NavLink className="navbar-brand font-weight-bolder" to="/"> <img src={footer_img}/> </NavLink>
            <a exact activeClassName="navactive" className="nav-link cart2" onClick={()=>setDisplay(true)} > <FontAwesomeIcon icon={faShoppingCart} /> <span>0</span> </a>
            <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNavAltMarkup" aria-controls="navbarNavAltMarkup" aria-expanded="false" aria-label="Toggle navigation">
            <span className="navbar-toggler-icon"></span>
            </button>
            <div className="collapse navbar-collapse" id="navbarNavAltMarkup">
            <div className="navbar-nav">
                <NavLink exact activeClassName="navactive" className="nav-link" to="/">Home</NavLink>
                <NavLink exact activeClassName="navactive" className="nav-link" to="/about">About</NavLink>
                <NavLink exact activeClassName="navactive" className="nav-link" to="/donate">Donate</NavLink>
                <NavLink activeClassName="navactive" className="nav-link" to="/shop">Shop</NavLink>
                <NavLink exact activeClassName="navactive" className="nav-link" to="/contact">contact</NavLink>
                <a exact activeClassName="navactive" className="nav-link cart" onClick={()=>setDisplay(true)} > <FontAwesomeIcon icon={faShoppingCart} /> <span>0</span> </a>
             </div>
            </div>
            </div>
        </nav>

    {
        display?<div className="cart_insid">
            <div className="row m-0">
                <div className="col-12 col-md-4 ml-auto cart_inside"  >
                    <div className="your_cart">
                        <h5>Your Cart</h5>
                        <button className="btn btn-close" onClick={()=>setDisplay(false)}></button>
                        
                    </div>
                    <hr className="m-0"/>
                <p>No items found</p>
                </div>
            </div>
        </div>:null
    }
        </>
    );
};

export default Navbar;