import React from "react";

const Donate =() =>{
    return(
        <>
         <div className="donation_sec1 text-center">
            <h1>Here at Acme Outdoors</h1>
            <h3>every dollar counts</h3>
            <p>Acme Outdoors is more than just a company, we're a community of people who care for one another and for our city. During this time, due to shelter in place orders, only a select few of our staff are able to work. Any donations you make to Acme will help make sure our employees are cared for and can stay safe in these uncertain times.</p>
        </div>
        <div className="donation_sec2 mt-5">
            <div className="row m-0">
                <div className="col-12 col-md-6 m-auto btn_group">
                    <button className="btn block-btn">Donate $100</button>
                    <button className="btn block-btn">Donate $75</button>
                    <button className="btn block-btn">Donate $50</button>
                    <button className="btn block-btn">Donate $30</button>
                    <button className="btn block-btn">Donate $10</button>
                    <button className="btn block-btn">Donate $5</button>
                </div>
            </div>
        </div>
        </>

    );
};

export default Donate;