import React from "react";
import footer_img from "./img/logo2.svg";
import icon1 from "./img/twitter.svg";
import icon2 from "./img/facebook.svg";
import icon3 from "./img/insta.svg";

const Footer =() =>{
    return(
        <>
        <div className="footer">
            <img src={footer_img} title="logo"/>
            <div className="footer_icons">
            <img src={ icon1 } title="twitter"/>
            <img src={ icon2 } title="facebook"/>
            <img src={ icon3 } title="instagram"/>
            </div>     
        </div>
        </>

    );
};

export default Footer;